import React, {Component} from 'react'
import ReactDOM from 'react-dom'
import {withStyles} from 'material-ui/styles';
import Slide from 'material-ui/transitions/Slide';
import './style.less'
import classnames from 'classnames'
const styles = theme => ({
    success: {
        backgroundColor: '#13CE66'
    },
    warn: {
        backgroundColor: '#F7BA2A'
    },
    info: {
        backgroundColor: '#20A0FF'
    },
    error: {
        backgroundColor: '#FF4949'
    }
});
let Icon = {
    'success': 'fa-check',
    'warn': 'fa-exclamation-triangle',
    'info': 'fa-info',
    'error': 'fa-close'
}
let index = 0;
let div = [];
@withStyles(styles)
export class Message extends Component {
    constructor(props) {
        super(props)
        this.state = {
            index: props.index
        }
    }

    componentDidMount() {
        let that = this
        setTimeout(function () {
            document.body.removeChild(div[that.props.index])
            that.props.callback && that.props.callback()
        }, 2000)
    }

    render() {
        const {classes} = this.props
        return (
            <Slide direction="down" in={true}>
                <div className="yh-message-box">
                    <div className="yh-message" style={{transform:'xxx'}}>
                        <div className={classnames('yh-message-icon',classes[this.props.type])}>
                            <i className={classnames('fa',Icon[this.props.type])} aria-hidden="true"></i>
                        </div>
                        <div className="yh-message-group"><p>{this.props.msg}</p></div>
                    </div>
                </div>
            </Slide>
        )
    }
}
let show = function (options) {
    let defaultOptions = {
        type: 'success',
        index: index++
    }
    Object.assign(defaultOptions, options || {})
    var dom = document.createElement('div')
    document.body.appendChild(dom);
    div.push(dom)
    ReactDOM.render(<Message {...defaultOptions}/>, dom);
}
export default {
    success: function (str, callback) {
        show({msg: str, type: 'success', callback})
    },
    warn: function (str, callback) {
        show({msg: str, type: 'warn', callback})
    },
    info: function (str, callback) {
        show({msg: str, type: 'info', callback})
    },
    error: function (str, callback) {
        show({msg: str, type: 'error', callback})
    },
    show: show
}




