import axios from 'axios'
// import qs from 'qs'
import _ from 'lodash'

var cookieStore = require('store')
var pkg = require('../../package.json')
var Ewell = {
    sources: []
}
let getDataAuthByKey = function (paramsKey) {
    var authData = window.USERAUTH;
    var opts = {}
    if (authData) {
        var obj = _.find(USERAUTH, function (o) {
            return o.permission === paramsKey
        })
        obj && obj.dataRoles.forEach(function (item) {
            opts[item.key] = item.name
        })
    }
    return opts
}
/**
 * 功能：axios 基础配置
 * 返回：配置好的增强版本 axios
 * */
var baseUrl = '/integration-authority-web';
//axios.defaults.headers.post['Content-Type'] = 'application/json';
var instance = axios.create({
    baseURL: baseUrl,
    timeout: 60000,
    headers: {
        'Content-Type': 'application/json'
    },
    transformResponse: [function (data) {
        if (JSON.parse(data).httpCode === 401) {
            cookieStore.clearAll();
            window.location.href = "/authority/login";
        } else if (JSON.parse(data).httpCode === 500) {
            window.location.href = "/authority/pageError";
        }
        return JSON.parse(data);
    }],
    validateStatus: function (status) {
        if (status < 200 || status > 300) {
            // cookieStore.clearAll();
            // window.location.href = "/authorityManagement/login";
            window.location.href = "/authority/pageError";
        }
        return status
    }
})

export const post = function (url, opts = {}, paramsKey) {
    let DataAuth = getDataAuthByKey(paramsKey)
    Object.assign(opts, DataAuth)
    if (cookieStore.get('userInfo')) {
        instance.defaults.headers.common['Authorization'] = cookieStore.get('userInfo').token;
    }
    var CancelToken = axios.CancelToken;
    var token = new CancelToken(function executor(c) {
        Ewell.sources.push(c)
    })
    return instance.post(url, {
        ...opts
    }, {
        cancelToken: token,
    })
}
export const get = function (url, opts, paramsKey) {
    let DataAuth = getDataAuthByKey(paramsKey)
    Object.assign(opts, DataAuth)
    if (cookieStore.get('userInfo')) {
        instance.defaults.headers.common['Authorization'] = cookieStore.get('userInfo').data;
    }
    var CancelToken = axios.CancelToken;
    var token = new CancelToken(function executor(c) {
        Ewell.sources.push(c)
    })
    return instance.get(url, {
        params: {
            ...opts,
        },
        cancelToken: token,
    })
}
export const all = axios.all
export const spread = axios.spread


